.wrapper {
  margin: 0 auto;
  width: 1022px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.tabs-nav {
  display: flex;
  justify-content: space-between;
}
.tabs-nav__item {
  text-align: center;
  cursor: pointer;
  flex: 1;
  padding: 20px;
  border-bottom: 1xp solid #ebe6e6;
}
.tabs-nav__item:not(.is-active) {
  &:hover {
    background-color: rgb(241, 233, 233);
    transition: all 0.4s ease-in;
  }
}
.tabs-nav__item.is-active {
  border: 1xp solid #e5e5e5;
  border-bottom: transparent;
}

.tab {
  border-top: transparent;
  display: none;
  padding: 20px;
}
.tabs__content {
  border: 1xp solid #e5e5e5;
  border-top: transparent;
  padding: 0 50px 30px;
}
.tab.is-active {
  display: block;
}
.tabs__content-img {
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
}
.tabs__nav-text {
  color: #676666;
  font-family: "Segoe";
  font-size: 27px;
  font-weight: 300;
}
.tabs__content-title {
  color: #4f4f4f;
  font-family: "Segoe";
  font-size: 39px;
  font-weight: 300;
  margin-bottom: 20px;
}
.tabs__content-text {
  color: #818181;
  font-family: "Univers";
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  margin-bottom: 20px;
}
.tabs__content-subtxet {
  color: #818181;
  font-family: "Univers";
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}

.tabs-nav__item.is-active {
  padding: 17px;
}


.left{
text-align: left;
}
.right{
  text-align: right;
  }
