.forms {
  border-top: 3px solid #e5e5e5;
  &__inner {
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 35px;
  }

  &__login {
    width: 470px;
    background-color: #e67624;
    border-radius: 4px;
  }

  &__login-title {
    padding-top: 5px;
    padding-left: 20px;
    color: #ffffff;
    font-family: "Segoe";
    font-size: 28px;
    font-weight: 300;
    padding-bottom: 15px;
  }

  &__login-btn {
    width: 95px;
    height: 30px;
    display: inline-block;
    align-items: center;
    justify-content: center;
    background-color: #444444;
    color: #ffffff;
    font-family: "Segoe";
    font-size: 16px;
    font-weight: 300;
    cursor: pointer;
    &:hover {
      background-color: #fff;
      color: black;
      font-weight: 600;
      transition: all 0.3s;
    }
  }

  &__sub {
    border-radius: 4px;
    width: 470px;
    background-color: #01d4f3;
  }

  &__sub-title {
    padding-top: 5px;
    padding-left: 20px;
    color: #ffffff;
    font-family: "Segoe";
    font-size: 28px;
    padding-bottom: 15px;
    font-weight: 300;
  }

  &__sub-btn {
    width: 111px;
    height: 30px;
    background-color: #444444;
    color: #ffffff;
    font-family: "Segoe";
    font-size: 16px;
    font-weight: 300;
    cursor: pointer;

    &:hover {
      background-color: #fff;
      color: black;
      font-weight: 600;
      transition: all 0.3s;
    }
  }
  &__name {
    width: 155px;
    margin-left: 20px;
    margin-right: 5px;
    margin-bottom: 20px;
    height: 26px;
    padding-left: 15px;
  }
  &__password {
    width: 150px;
    height: 26px;
    margin-right: 5px;
    padding-left: 15px;
  }
  &__email {
    height: 26px;
    margin-right: 40px;
    margin-left: 20px;
    width: 265px;
    padding-left: 15px;
  }
}
