@charset "UTF-8";
@import url(./vendor/slick.css);
:root {
  --color-black: #000;
  --color-white: #fff;
}

@font-face {
  font-family: "Arial";
  src: url("../fonts/Arial-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Segoe";
  src: url("../fonts/Segoe-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Segoe";
  src: url("../fonts/Segoe-Semibold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Univers";
  src: url("../fonts/Univers-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

