h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "asd", sans-serif;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1054px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.wrapper {
  margin: 0 auto;
  width: 1022px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.tabs-nav {
  display: flex;
  justify-content: space-between;
}

.tabs-nav__item {
  text-align: center;
  cursor: pointer;
  flex: 1;
  padding: 20px;
  border-bottom: 1xp solid #ebe6e6;
}

.tabs-nav__item:not(.is-active):hover {
  background-color: #f1e9e9;
  transition: all 0.4s ease-in;
}

.tabs-nav__item.is-active {
  border: 1xp solid #e5e5e5;
  border-bottom: transparent;
}

.tab {
  border-top: transparent;
  display: none;
  padding: 20px;
}

.tabs__content {
  border: 1xp solid #e5e5e5;
  border-top: transparent;
  padding: 0 50px 30px;
}

.tab.is-active {
  display: block;
}

.tabs__content-img {
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
}

.tabs__nav-text {
  color: #676666;
  font-family: "Segoe";
  font-size: 27px;
  font-weight: 300;
}

.tabs__content-title {
  color: #4f4f4f;
  font-family: "Segoe";
  font-size: 39px;
  font-weight: 300;
  margin-bottom: 20px;
}

.tabs__content-text {
  color: #818181;
  font-family: "Univers";
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  margin-bottom: 20px;
}

.tabs__content-subtxet {
  color: #818181;
  font-family: "Univers";
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
}

.tabs-nav__item.is-active {
  padding: 17px;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.questions__inner {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;
}

.questions__item + .questions__item {
  padding-left: 50px;
}

.questions__title {
  color: #4f4f4f;
  font-family: "Segoe";
  font-size: 33px;
  font-weight: 300;
  margin-bottom: 30px;
}

.questions__text {
  color: #8a8888;
  font-family: "Univers";
  font-size: 16px;
  font-weight: 300;
  padding-left: 35px;
  padding-bottom: 20px;
  position: relative;
}

.questions__text:last-child {
  padding-bottom: 0;
}

.questions__text::before {
  content: "";
  position: absolute;
  background-image: url("../img/Shape_15.png");
  width: 18px;
  height: 18px;
  left: 0;
  top: 0;
}

.forms {
  border-top: 3px solid #e5e5e5;
}

.forms__inner {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  padding-bottom: 35px;
}

.forms__login {
  width: 470px;
  background-color: #e67624;
  border-radius: 4px;
}

.forms__login-title {
  padding-top: 5px;
  padding-left: 20px;
  color: #ffffff;
  font-family: "Segoe";
  font-size: 28px;
  font-weight: 300;
  padding-bottom: 15px;
}

.forms__login-btn {
  width: 95px;
  height: 30px;
  display: inline-block;
  align-items: center;
  justify-content: center;
  background-color: #444444;
  color: #ffffff;
  font-family: "Segoe";
  font-size: 16px;
  font-weight: 300;
  cursor: pointer;
}

.forms__login-btn:hover {
  background-color: #fff;
  color: black;
  font-weight: 600;
  transition: all 0.3s;
}

.forms__sub {
  border-radius: 4px;
  width: 470px;
  background-color: #01d4f3;
}

.forms__sub-title {
  padding-top: 5px;
  padding-left: 20px;
  color: #ffffff;
  font-family: "Segoe";
  font-size: 28px;
  padding-bottom: 15px;
  font-weight: 300;
}

.forms__sub-btn {
  width: 111px;
  height: 30px;
  background-color: #444444;
  color: #ffffff;
  font-family: "Segoe";
  font-size: 16px;
  font-weight: 300;
  cursor: pointer;
}

.forms__sub-btn:hover {
  background-color: #fff;
  color: black;
  font-weight: 600;
  transition: all 0.3s;
}

.forms__name {
  width: 155px;
  margin-left: 20px;
  margin-right: 5px;
  margin-bottom: 20px;
  height: 26px;
  padding-left: 15px;
}

.forms__password {
  width: 150px;
  height: 26px;
  margin-right: 5px;
  padding-left: 15px;
}

.forms__email {
  height: 26px;
  margin-right: 40px;
  margin-left: 20px;
  width: 265px;
  padding-left: 15px;
}

.footer {
  background-image: url("../img/footer.png");
  height: 100%;
  width: 100%;
}

.footer__inner {
  display: flex;
  padding: 0 30px;
  padding-bottom: 50px;
}

.footer__item {
  padding-top: 30px;
}

.footer__item:nth-child(1) {
  margin-right: 90px;
  width: 100px;
}

.footer__item:nth-child(2) {
  margin-right: 90px;
}

.footer__item:nth-child(3) {
  width: 316px;
  margin-right: 40px;
}

.footer__title {
  position: relative;
}

.footer__title::before {
  content: "";
  position: absolute;
  background-image: url("../img/Layer_38_copy_2.png");
  width: 16px;
  height: 16px;
  top: 7px;
  right: 124px;
}

.footer__title::after {
  content: "";
  position: absolute;
  background-image: url("../img/Layer_38_copy.png");
  width: 16px;
  height: 16px;
  top: 7px;
  right: 98px;
}

.footer__item-title {
  color: #ffffff;
  font-family: "Segoe";
  font-size: 22px;
  font-weight: 300;
  margin-bottom: 15px;
}

.footer__item-text {
  color: #ffffff;
  font-family: "Arial";
  font-size: 13px;
  line-height: 22px;
  opacity: 0.5;
}

.footer__item-subtext {
  opacity: 0.66;
  color: #ffffff;
  font-family: "Arial";
  font-size: 13px;
  line-height: 22px;
  font-weight: 600;
}

.footer__logo {
  padding-top: 40px;
  margin-bottom: 35px;
}

.footer__copy {
  opacity: 0.8;
  color: #ffffff;
  font-family: "Arial";
  font-size: 13px;
  font-weight: 400;
}

/*Убрать полосу прокрутки для всей страницы*/
::-webkit-scrollbar {
  width: 0;
}

input {
  outline: none;
}

.header {
  position: relative;
}

.header::before {
  content: "";
  position: absolute;
  background-image: url("../img/bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 490px;
  z-index: -1;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;
}

.header__inner img {
  padding-top: 50px;
}

.nav__list {
  display: flex;
}

.nav__link {
  padding-right: 25px;
  color: #ffffff;
  font-family: "Segoe";
  font-size: 23px;
  font-weight: 300;
}

.nav__link a {
  display: block;
  margin: auto;
  padding: 1rem 1.25rem;
  font-family: sans-serif;
  font-size: 23px;
  text-decoration: none;
  color: white;
  position: relative;
  transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
  z-index: 1;
}

.nav__link a:after {
  content: "";
  display: block;
  height: 2px;
  position: absolute;
  bottom: 0;
  right: 1.25rem;
  left: 1.25rem;
  background-color: #ffffff;
  transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
  transform-origin: bottom center;
  z-index: -1;
}

.nav__link a:hover {
  color: #2d2d2d;
}

.nav__link a:hover:after {
  right: 0;
  left: 0;
  height: 100%;
}

/* Ограничивающая оболочка */
.slick-list {
  overflow: hidden;
}

/* Лента слайдов */
.slick-track {
  display: flex;
  align-items: flex-start;
}

/* Конкретный слайдер */
.slider {
  padding: 0 50px;
  position: relative;
}

.slider .slick-arrow {
  position: absolute;
  top: 50%;
  /* половина высоты */
  margin: -7px 0 0 0;
  width: 50px;
  height: 50px;
  z-index: 10;
  font-size: 0;
  border: none;
}

.slider .slick-arrow.slick-prev {
  background: url("../img/left.png") 0 0 no-repeat;
  top: 130px;
  cursor: pointer;
  left: 0;
}

.slider .slick-arrow.slick-next {
  background: url("../img/right.png") 0 0 no-repeat;
  right: 0;
  top: 130px;
  cursor: pointer;
}

.slider .slick-dots {
  padding-top: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider .slick-dots li {
  margin: 0 10px;
}

.slider .slick-dots li.slick-active button {
  border: 2px solid #1ca8dd;
  background-color: inherit;
}

.slider .slick-dots li button {
  font-size: 0;
  width: 12px;
  height: 12px;
  border: 2px solid #dedfe0;
  background-color: inherit;
  border-radius: 50%;
}

.slider .slick-arrow.slick-disabled {
  opacity: 0.2;
}

.slider__item {
  display: flex !important;
  max-width: 740px !important;
  margin: 0 auto;
}

.slider__item-text {
  padding-top: 45px;
  padding-left: 65px;
  color: #ffffff;
  font-family: "Segoe";
}

.slider__item-title {
  font-size: 65px;
  font-weight: 600;
}

.slider__item-subtitle {
  font-size: 45px;
  font-weight: 300;
  margin-bottom: 30px;
}

.slider__item-btn {
  font-size: 23px;
  font-weight: 300;
  color: #ffffff;
  border: 2px solid rgba(0, 0, 0, 0.37);
  background-color: #236949;
  width: 138px;
  height: 38px;
  cursor: pointer;
  border-radius: 4px;
  position: relative;
  box-sizing: border-box;
  transition: all 500ms ease;
}

.slider__item-btn:hover {
  background: #df0e0e;
  color: #000000;
  box-shadow: inset 0 0 0 3px #df1313;
  font-weight: 600;
}
