.questions {
  &__inner {
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
  }

  &__item {
    & + & {
      padding-left: 50px;
    }
  }

  &__title {
    color: #4f4f4f;
    font-family: "Segoe";
    font-size: 33px;
    font-weight: 300;
    margin-bottom: 30px;
  }

  &__text {
    color: #8a8888;
    font-family: "Univers";
    font-size: 16px;
    font-weight: 300;
    padding-left: 35px;
    padding-bottom: 20px;
    position: relative;
    &:last-child {
      padding-bottom: 0;
    }
    &::before {
      content: "";
      position: absolute;
      background-image: url("../img/Shape_15.png");
      width: 18px;
      height: 18px;
      left: 0;
      top: 0;
    }
  }
}
