.header {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    background-image: url("../img/bg.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 490px;
    z-index: -1;
  }
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 60px;
    img {
      padding-top: 50px;
    }
  }
}

.nav__list {
  display: flex;
}
.nav__link {
  padding-right: 25px;
  color: #ffffff;
  font-family: "Segoe";
  font-size: 23px;
  font-weight: 300;
  a {
    display: block;
    margin: auto;
    padding: 1rem 1.25rem;
    font-family: sans-serif;
    font-size: 23px;
    text-decoration: none;
    color: white;
    position: relative;
    transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
    z-index: 1;

    &:after {
      content: "";
      display: block;
      height: 2px;
      position: absolute;
      bottom: 0;
      right: 1.25rem;
      left: 1.25rem;
      background-color: #ffffff;
      transition: all 0.3s cubic-bezier(0.2, 0, 0, 1);
      transform-origin: bottom center;
      z-index: -1;
    }

    &:hover {
      color: #2d2d2d;

      &:after {
        right: 0;
        left: 0;
        height: 100%;
      }
    }
  }
}

/* Ограничивающая оболочка */
.slick-list {
  overflow: hidden;
}
/* Лента слайдов */
.slick-track {
  display: flex;
  align-items: flex-start;
}

/* Конкретный слайдер */
.slider {
  padding: 0 50px;
  position: relative;
}
.slider .slick-arrow {
  position: absolute;
  top: 50%;
  /* половина высоты */
  margin: -7px 0 0 0;

  width: 50px;
  height: 50px;
  z-index: 10;
  font-size: 0;
  border: none;
}
.slider .slick-arrow.slick-prev {
  background: url("../img/left.png") 0 0 no-repeat;
  top: 130px;
  cursor: pointer;
  left: 0;
}
.slider .slick-arrow.slick-next {
  background: url("../img/right.png") 0 0 no-repeat;
  right: 0;
  top: 130px;
  cursor: pointer;
}

.slider .slick-dots {
  padding-top: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.slider .slick-dots li {
  margin: 0 10px;
}
.slider .slick-dots li.slick-active button {
  border: 2px solid #1ca8dd;
  background-color: inherit;
}
.slider .slick-dots li button {
  font-size: 0;
  width: 12px;
  height: 12px;
  border: 2px solid #dedfe0;
  background-color: inherit;
  border-radius: 50%;
}
.slider .slick-arrow.slick-disabled {
  opacity: 0.2;
}
.slider__item {
  display: flex !important;
  max-width: 740px !important;
  margin: 0 auto;
}
.slider {
  &__item-text {
    padding-top: 45px;
    padding-left: 65px;
    color: #ffffff;
    font-family: "Segoe";
  }

  &__item-title {
    font-size: 65px;
    font-weight: 600;
  }

  &__item-subtitle {
    font-size: 45px;
    font-weight: 300;
    margin-bottom: 30px;
  }

  &__item-btn {
    font-size: 23px;
    font-weight: 300;
    color: #ffffff;
    border: 2px solid rgba(0, 0, 0, 0.37);
    background-color: #236949;
    width: 138px;
    height: 38px;
    cursor: pointer;
    border-radius: 4px;
    position: relative;
    box-sizing: border-box;
    transition: all 500ms ease;
    &:hover {
      background: rgb(223, 14, 14);
      color: #000000;
      box-shadow: inset 0 0 0 3px #df1313;
      font-weight: 600;
    }
  }
}
