.footer {
  background-image: url("../img/footer.png");
  height: 100%;
  width: 100%;
  &__inner {
    display: flex;
    padding: 0 30px;
    padding-bottom: 50px;
  }

  &__item {
    padding-top: 30px;

    &:nth-child(1) {
      margin-right: 90px;
      width: 100px;
    }
    &:nth-child(2) {
      margin-right: 90px;
    }
    &:nth-child(3) {
      width: 316px;
      margin-right: 40px;
    }
  }
  &__title {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      background-image: url("../img/Layer_38_copy_2.png");
      width: 16px;
      height: 16px;
      top: 7px;
      right: 124px;
    }
    &::after{
      content: "";
      position: absolute;
      background-image: url("../img/Layer_38_copy.png");
      width: 16px;
      height: 16px;
      top: 7px;
      right: 98px;
    }
  }

  &__item-title {
    color: #ffffff;
    font-family: "Segoe";
    font-size: 22px;
    font-weight: 300;
    margin-bottom: 15px;
  }

  &__item-text {
    color: #ffffff;
    font-family: "Arial";
    font-size: 13px;
    line-height: 22px;
    opacity: 0.5;
  }

  &__item-subtext {
    opacity: 0.66;
    color: #ffffff;
    font-family: "Arial";
    font-size: 13px;
    line-height: 22px;
    font-weight: 600;
  }

  &__logo {
    padding-top: 40px;
    margin-bottom: 35px;
  }

  &__copy {
    opacity: 0.8;
    color: #ffffff;
    font-family: "Arial";
    font-size: 13px;
    font-weight: 400;
  }
}
